.todoItem{
    border-bottom: 1px solid #eee;
    display: flex;
    line-height: 3;
    padding: 0 30px;
    margin-top: 10px;
    cursor: pointer;
}
.remove{
    margin-right: 20px;
    color: red;
    font-weight: bold;
    opacity: 0;
}
.todoItem:hover .remove{
    opacity: 1;
}
.checked{
    text-decoration: line-through;
    color: #ccc;
}