.slick-track {
    display: flex;
    align-items: center;
}
.portfolioWrap a {
    display: block;
    color: black;
    font-size: 14px;
    font-weight: 700;
    width: 100px;
    line-height: 35px;
    background: #fed404;
    text-decoration: none;
    border-radius: 30px;
    margin: 30px auto;
}
.portfolioWrap h3>img{
    filter: drop-shadow(1px 1px 2px #fff);
}
.slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 10;
}

.slick-next,
.slick-prev {
    font-size: 0;
    line-height: 0;
    width: 32px;
    height: 32px;
}

.slick-prev {
    left: 0;
    background-image: url(../../public/icon/left_arrow.png);
    background-repeat: no-repeat;
    background-position: center center;
    border: none;
}

.slick-next {
    right: 0;
    background-image: url(../../public/icon/right_arrow.png);
    background-repeat: no-repeat;
    background-position: center center;
    border: none;
}

.slick-slide {
    text-align: center;
}

.portfolioWrap img {
    display: inline-block;
}

.portfolioWrap h3{
    text-shadow: 1px 1px 2px #000;
}