a{
    color: #666;
}
.aboutWrap{
    position: relative;
}
.aboutWrap>div{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: left;
}
.aboutWrap>div>.box:first-child>.imgBox img{
    border-radius: 50%;
}
.aImg{
    position: absolute;
    right: -15%;
    top: 10%;
    max-width: 600px;
}
.box{
    display: flex;
    justify-content: space-evenly;
    background: white;
    padding: 25px;
    border-radius: 10px;
    
    width: 32%;
    margin: 5px 0;
    box-sizing: border-box;
}
.box .imgBox{
    width: 64px;
    margin-right: 10px;
}
.box .imgBox>img{
    width: 100%;
}
.box .textBox{
    width: 100%;
    flex: 1;
}
.box .textBox strong{
    color: #000;
}
.box .textBox p{
    font-size: 14px;
    color: #666;
}