*{
  margin: 0;
  padding: 0;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
}
img{
  vertical-align: middle;
}
body{
  font-family: 'Nanum Gothic', sans-serif;
  background-image: url(../public/images/banner-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}
.Wrap{

}
.innerBox{
  max-width: 1200px;
  margin: 0 auto;
  color: white;
}
header{
  position: fixed;
  width: 100%;
  border-bottom: 1px solid #333;
  z-index: 10;
}
header>.innerBox{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.innerBox>.container{
  padding-top: 100px;
}
#nav{
  display: flex;
}
#nav>li{
  margin-left: 100px;
}
#nav>li>a{
  position: relative;
  display: block;
  font-weight: 400;
  color: white;
  padding: 5px 10px;
  text-shadow: 1px 1px 2px black;
  z-index: 1;
  transition: 0.5s;
  /* background-color: antiquewhite; */
}
#nav>li>a::before{
  content: "";
  display: block;
  position: absolute;
  top:0;
  left: 50%;
  width: 0%;
  transform: translateX(-50%);
  height: 100%;
  border-radius: 20px;
  background-color: white;
  z-index: -1;
  transition: 0.5s;
}
#nav>li:hover>a{
  color: black;
}
#nav>li:hover>a::before{
  width: 100%;
}
.logo{
  width: 200px;
  padding: 10px 0;
}
.logo img{
  width: 100%;
  filter: drop-shadow(1px 1px 3px rgba(0,0,0,0.5));
}

.homeWrap{
  position: relative;
  height: 100vh;
}
.homeWrap>.innerBox{
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}
.homeWrap .faceImg{

  max-width: 480px;
}
.homeWrap .faceImg img{
  width: 100%;
  border-radius: 50%;
}
.homeText{

}
.homeText>p{
  font-size: 60px;
  font-weight: 800;
}
.homeText>p>span{
  display: block;
}
.homeText>p>span:nth-child(3){
  color: #6231ad;
}
