.skillWrap section{
    padding-top: 30px;
}
.skillWrap section>div{
    display: flex;
    /* justify-content: space-between; */
    flex-wrap: wrap;
}
.skillWrap section>div .box{
    display: block;
    text-align: center;
    margin: 10px 1%;
    width: 23%;
    border-radius: 50px;
}
.skillWrap section>div .imgBox{
    width: 100%;
    box-sizing: border-box;
}
.skillWrap section>div .imgBox img{
    width: 100%;
    filter: drop-shadow(2px 2px 3px rgba(0,0,0,0.5));
}